"use client"

import type React from "react"

import { useState } from "react"
import { useRouter } from "next/navigation"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Textarea } from "@/components/ui/textarea"
import { Building2, ChevronLeft } from "lucide-react"
import { ThemeToggle } from "@/components/theme-toggle"
import { AuthGuard } from "@/components/auth-guard"
import { auth, db } from "@/lib/firebase" // Ajustez le chemin selon votre config
import { collection, addDoc, serverTimestamp } from "firebase/firestore"
import { useToast } from "@/components/ui/use-toast" // Optionnel pour les notifications

export default function CreateCompanyPage() {
  const router = useRouter()
  const { toast } = useToast() // Optionnel
  const [isLoading, setIsLoading] = useState(false)
  const [formData, setFormData] = useState({
    name: "",
    industry: "",
    description: "",
    address: "",
  })

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    setIsLoading(true)

    try {
      const user = auth.currentUser
      
      if (!user) {
        throw new Error("Utilisateur non authentifié")
      }

      // Créer l'entreprise dans Firestore
      const companyData = {
        name: formData.name,
        industry: formData.industry,
        description: formData.description,
        address: formData.address,
        ownerId: user.uid, // ID de l'utilisateur qui crée l'entreprise
        ownerEmail: user.email,
        createdAt: serverTimestamp(),
        updatedAt: serverTimestamp(),
      }

      const docRef = await addDoc(collection(db, "companies"), companyData)

      console.log("Entreprise créée avec l'ID:", docRef.id)

      // Notification de succès (optionnel)
      toast?.({
        title: "Succès !",
        description: "L'entreprise a été créée avec succès.",
      })

      // Redirection vers le dashboard
      router.push("/")
    } catch (error) {
      console.error("Erreur lors de la création de l'entreprise:", error)
      
      // Notification d'erreur (optionnel)
      toast?.({
        title: "Erreur",
        description: "Une erreur est survenue lors de la création de l'entreprise.",
        variant: "destructive",
      })
    } finally {
      setIsLoading(false)
    }
  }

  const handleChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
    setFormData((prev) => ({
      ...prev,
      [e.target.name]: e.target.value,
    }))
  }

  return (
    <AuthGuard>
      <div className="min-h-screen flex items-center justify-center bg-background p-6">
        {/* Theme toggle in top right */}
        <div className="fixed top-4 right-4 z-50">
          <ThemeToggle />
        </div>

        <div className="w-full max-w-2xl space-y-8">
          {/* Header */}
          <div className="text-center space-y-4">
            <div className="mx-auto w-16 h-16 bg-primary rounded-2xl flex items-center justify-center shadow-lg shadow-primary/20">
              <Building2 className="w-8 h-8 text-primary-foreground" />
            </div>
            <div>
              <h1 className="text-3xl font-bold text-foreground">Ajouter une Entreprise</h1>
              <p className="text-muted-foreground mt-2">Créez un nouvel espace de travail pour votre organisation</p>
            </div>
          </div>

          {/* Form */}
          <div className="bg-card border border-border rounded-2xl shadow-xl p-8">
            <form onSubmit={handleSubmit} className="space-y-6">
              <div className="space-y-2">
                <Label htmlFor="name" className="text-sm font-medium">
                  Nom de l'entreprise<span className="text-destructive">*</span>
                </Label>
                <Input
                  id="name"
                  name="name"
                  type="text"
                  placeholder="Entrez le nom de l'entreprise"
                  value={formData.name}
                  onChange={handleChange}
                  required
                  className="h-11 bg-secondary/50 border-border"
                />
              </div>

              <div className="space-y-2">
                <Label htmlFor="industry" className="text-sm font-medium">
                  Domaine <span className="text-destructive">*</span>
                </Label>
                <Input
                  id="industry"
                  name="industry"
                  type="text"
                  placeholder="ex: Technologie, Santé, Finance"
                  value={formData.industry}
                  onChange={handleChange}
                  required
                  className="h-11 bg-secondary/50 border-border"
                />
              </div>

              <div className="space-y-2">
                <Label htmlFor="description" className="text-sm font-medium">
                  Description
                </Label>
                <Textarea
                  id="description"
                  name="description"
                  placeholder="Brève description de votre entreprise"
                  value={formData.description}
                  onChange={handleChange}
                  rows={4}
                  className="bg-secondary/50 border-border resize-none"
                />
              </div>

              <div className="space-y-2">
                <Label htmlFor="address" className="text-sm font-medium">
                  Adresse
                </Label>
                <Input
                  id="address"
                  name="address"
                  type="text"
                  placeholder="Adresse de l'entreprise"
                  value={formData.address}
                  onChange={handleChange}
                  className="h-11 bg-secondary/50 border-border"
                />
              </div>

              <div className="flex items-center gap-4 pt-4">
                <Button
                  type="button"
                  variant="outline"
                  className="flex-1 h-11 bg-transparent"
                  onClick={() => router.push("/select-society")}
                  disabled={isLoading}
                >
                  <ChevronLeft className="w-4 h-4 mr-2" />
                  Retour
                </Button>
                <Button type="submit" className="flex-1 h-11" disabled={isLoading}>
                  {isLoading ? "Création..." : "Créer l'Entreprise"}
                </Button>
              </div>
            </form>
          </div>
        </div>
      </div>
    </AuthGuard>
  )
}